import React from "react";
import { Link } from "react-router-dom";

function Home() {
  return (
    <>
      <h1>Home</h1>
      <ul>
        <li>
          <Link to="/review">Review</Link> :  Review .
        </li>
        <li>
          <Link to="/wordcloud">Wordcloud</Link> :  Wordcloud .
        </li>
      </ul>
    </>
  );
}

export default Home;
